<template>
  <a
    :href="property.property_post_link"
    @click="sendGAEvent('View Property', 'click', property.property_name)"
  >
    <v-card
      max-width="410"
      :height="!hideImage ? 377 : 126"
      hover
      :tile="hideImage"
    >
      <v-img
        v-if="!hideImage"
        :src="featuredImage"
        :srcset="featuredImageSrcset"
        height="245px"
        max-width="500px"
        sizes="(max-width: 500px) 500px, 500px"
      >
        <v-banner
          v-if="property.property_specials_message"
          elevation="10"
          icon="mdi-star"
          color="primary"
          dark
          class="rentpress-property-card-special-banner rentpress-inherited-font-family text-body-2"
        >
          <div>
            <span class="font-weight-black">SPECIAL:</span>
            {{ property.property_specials_message }}
          </div>
        </v-banner>
      </v-img>
      <v-card-title
        primary-title
        class="rentpress-inherited-font-family primary--text font-weight-bold rentpress-no-wrap-text"
      >
        {{ property.property_name }}
      </v-card-title>
      <v-card-subtitle align="left" class="rentpress-inherited-font-family">
        {{ property.property_city }}, {{ property.property_state }}
      </v-card-subtitle>
      <v-card-text>
        <v-row justify="center" no-gutters>
          <v-col
            v-if="displayBedTypes"
            cols="4"
            class="rentpress-inherited-font-family"
          >
            <v-icon small color="primary" class="pr-1">mdi-bed-double</v-icon
            >{{ displayBedTypes }}
          </v-col>
          <v-col cols="5" class="rentpress-inherited-font-family text-center">
            <div
              v-if="
                property.property_rent_type_selection == 'Minimum - Maximum' &&
                  property.property_rent_min &&
                  property.property_rent_max &&
                  property.property_rent_min != property.property_rent_max
              "
            >
              <v-icon small color="primary" class="pr-1">mdi-tag</v-icon>
              {{ formatter.format(property.property_rent_min) }} -
              {{ formatter.format(property.property_rent_max) }}
            </div>
            <div v-else-if="property.property_rent_type_selection_cost">
              <v-icon small color="primary" class="pr-1">mdi-tag</v-icon>
              Starting at
              {{ formatter.format(property.property_rent_type_selection_cost) }}
            </div>
            <div v-else>
              <v-icon small color="primary" class="pr-1">mdi-tag</v-icon>
              {{
                options.rentpress_disable_pricing_section_disable_pricing_message
              }}
            </div>
          </v-col>
          <v-col cols="3" class="rentpress-inherited-font-family text-right">
            <v-icon
              v-if="
                property.property_terms &&
                  property.property_terms.toUpperCase().includes('CAT')
              "
              color="primary"
            >
              mdi-cat
            </v-icon>
            <v-icon
              v-if="
                property.property_terms &&
                  property.property_terms.toUpperCase().includes('DOG')
              "
              color="primary"
            >
              mdi-dog
            </v-icon>
          </v-col>
        </v-row>
      </v-card-text>
      <script v-html="jsonld" type="application/ld+json"></script>
    </v-card>
  </a>
</template>
<script>
export default {
  name: "property-card",
  data: function() {
    return {
      bed_types: [],
      displayBedTypes: "",
      featuredImage: this.setFeaturedImage(),
      featuredImageSrcset: this.setFeaturedImageSrcset(),
      jsonld: {
        // do schema here
        "@context": "http://schema.org/",
        "@type": "ApartmentComplex",
        address: {
          "@type": "PostalAddress",
          streetAddress: this.property.property_address,
          addressLocality: this.property.property_city,
          addressRegion: this.property.property_state,
          postalCode: this.property.property_zip
        },
        geo: {
          "@type": "GeoCoordinates",
          latitude: this.property.property_latitude,
          longitude: this.property.property_longitude
        },
        telephone: this.property.property_phone_number,
        image: this.property.property_featured_image_src,
        name: this.property.property_name,
        numberOfAvailableAccommodationUnits: this.property
          .property_available_floorplans,
        tourBookingPage: this.property.property_tour_url,
        amenityFeature: this.property.property_terms,
        petsAllowed: this.displayPetText,
        url: this.property.property_post_link,
        sameAs: [
          this.property.property_website,
          this.property.property_facebook_link,
          this.property.property_twitter_link,
          this.property.property_instagram_link
        ]
      }
    };
  },
  props: {
    property: Object,
    options: Object,
    hideImage: {
      type: Boolean,
      default: false
    },
    formatter: Intl.NumberFormat
  },
  methods: {
    navigateToPost: function(post_id) {
      window.location = "/?page_id=" + post_id;
    },
    setFeaturedImage() {
      return this.property.property_featured_image_src &&
        this.property.property_featured_image_src != ""
        ? this.property.property_featured_image_src
        : this.options.rentpress_default_property_image_section;
    },
    setFeaturedImageSrcset() {
      return this.property.property_featured_image_srcset &&
        this.property.property_featured_image_srcset != ""
        ? this.property.property_featured_image_srcset
        : this.options.rentpress_default_property_image_section;
    }
  },
  mounted: function() {
    if (this.property.property_bed_types) {
      this.bed_types = JSON.parse(this.property.property_bed_types).sort();
    }

    if (this.bed_types.length) {
      if (this.bed_types[0] == 0) {
        this.displayBedTypes = "Studio";
      } else {
        this.displayBedTypes = this.bed_types[0] + " Bed";
      }

      if (this.bed_types.length > 1) {
        this.displayBedTypes +=
          " - " + this.bed_types[this.bed_types.length - 1] + " Bed";
      }
    }
  }
};
</script>

<style lang="css">
.rentpress-property-card-special-banner {
  opacity: 0.95;
}
.rentpress-property-card-special-banner .v-banner__wrapper {
  padding: 4px;
}
.rentpress-property-card-special-banner .v-banner__icon {
  margin: 0px;
}
</style>
