<template>
  <v-card
    height="162px"
    width="162px"
    :color="isActive ? 'primary' : ''"
    :dark="isActive"
    outlined
    ripple
    hover
    class="rentpress-shortcode-unit-card pa-2"
  >
    <v-card-text
      class="rentpress-inherited-font-family text-center pa-0 ma-0 text-h6"
      align="center"
    >
      <div
        :class="
          isActive
            ? 'font-weight-bold rentpress-no-wrap-text'
            : 'font-weight-bold secondary--text rentpress-no-wrap-text'
        "
      >
        {{ unit.unit_name }}
      </div>
      <div
        v-if="unit.unit_rent_type_selection_cost && !disabledPrice"
        class="rentpress-inherited-font-family text-subtitle-1"
      >
        {{ this.formatter.format(unit.unit_rent_type_selection_cost) }}/mo
      </div>
      <div v-else>
        {{ options.rentpress_disable_pricing_section_disable_pricing_message }}
      </div>
      <div
        v-if="unit.unit_sqft"
        class="rentpress-inherited-font-family text-subtitle-1"
      >
        {{ unit.unit_sqft }} sq. ft.
      </div>
      <div
        v-if="unitAvailable"
        class="rentpress-inherited-font-family text-subtitle-1 font-weight-bold"
      >
        Available Now
      </div>
      <div
        v-else
        class="rentpress-inherited-font-family text-subtitle-1 font-weight-bold"
      >
        Available On<br />{{ unit.unit_ready_date }}
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "unit-card",
  data: function() {
    return {
      unitAvailable: false
    };
  },
  props: {
    unit: Object,
    options: Object,
    isActive: Boolean,
    formatter: Intl.NumberFormat,
    disabledPrice: {
      type: Boolean,
      default: false
    }
  },
  computed: {},
  mounted: function() {
    var today = new Date();
    this.unitAvailable = Date.parse(this.unit.unit_ready_date) < today;
  }
};
</script>
